'use client'

import React, { useState } from 'react';
import styles from './public_services.module.css';

function Public_services() {
    const [formData, setFormData] = useState({
        title: '',
        description: '',
        category: '',
        basePrice: '',
        packages: '',
        featured: false,
    });
    const [previewImage, setPreviewImage] = useState<string | null>(null);
    const [errors, setErrors] = useState<Record<string, string>>({});

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        const { name, value, type } = e.target as HTMLInputElement;
        const checked = type === 'checkbox' ? (e.target as HTMLInputElement).checked : undefined;

        setFormData(prev => ({
            ...prev,
            [name]: type === 'checkbox' ? checked : value
        }));
    };

    const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (file) {
            setPreviewImage(URL.createObjectURL(file));
        }
    };

    const validateForm = () => {
        const newErrors: Record<string, string> = {};

        if (!formData.title.trim()) newErrors.title = 'Título é obrigatório';
        if (!formData.description.trim()) newErrors.description = 'Descrição é obrigatória';
        if (!formData.category) newErrors.category = 'Selecione uma categoria';
        if (!formData.basePrice || isNaN(Number(formData.basePrice))) newErrors.basePrice = 'Preço inválido';

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();

        if (validateForm()) {
            // Aqui você faria o submit do formulário
            console.log('Dados do formulário:', formData);
            alert('Formulário enviado com sucesso!');
        }
    };


    return (
        <>
            <form className={styles.form} onSubmit={handleSubmit} >
                <div className={styles.formGroup}>
                    <label className={styles.label}>
                        <span>titulo</span>
                        <input
                            type='text'
                            name='title'
                            value={formData.title}
                            onChange={handleChange}
                            className={styles.input}
                            required
                        />
                    </label>
                </div>
            </form>
        </>
    )

}
export default Public_services;